import React, {Fragment} from 'react';

const Drink = ({elemento}) => {

    const {strDrink, strDrinkThumb, idDrink} = elemento;

    const background = {
        backgroundImage: `url('${strDrinkThumb}')`
    }

    return (
        <Fragment>
          <div className="drink" style={background}>
            <p className="name-drink">{strDrink}</p>
            <button className="button-ing">How to make<br></br> <span>{strDrink}</span></button>
        </div>
     
        
        </Fragment>
      
        
      );
}
 
export default Drink;